.head {
    width: 100%;
    height: 48px;
    box-shadow: 0px 1px 50px rgba(51, 51, 51, 0.3);
    background-color: rgb(24, 24, 24);
    margin-bottom: 10px;
}
.row{
    display: flex;
    justify-content:space-between;
    align-items: center;
}
.title{
    margin-top: 7px;
    font-size: 16px;
    color: var(--color-primary);
}

.logo{
    margin-top: 9px;
    width: 30px;
    height: 30px;
    background-color: transparent;
    margin-right: 15px;
}

.loginBlock{
    justify-self: right;
    margin-left: auto;
    align-self: center;
}
.link{
    color: var(--color-primary);
}


